import cmdNavBar from '@/components/cmd-nav-bar/cmd-nav-bar.vue';
import cmdPageBody from '@/components/cmd-page-body/cmd-page-body.vue';
import cmdTransition from '@/components/cmd-transition/cmd-transition.vue';
import cmdInput from '@/components/cmd-input/cmd-input.vue';
import { mapState } from 'vuex';
export default {
	components: {
		cmdNavBar,
		cmdPageBody,
		cmdTransition,
		cmdInput
	},
	computed: {
		...mapState(['serverUrl'])
	},
	data() {
		return {
			mobile: {
				phone: '',
				code: '',
				passwordOne: '',
				passwordTwo: ''
			},
			passwordReg: /^\w+$/,
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
			safety: {
				time: 60,
				state: false,
				interval: ''
			},
			modifyMobile: false
		};
	},

	watch: {
		/**
		 * 监听表单数值
		 */
		mobile: {
			handler(newValue) {
				if (this.phoneReg.test(newValue.phone) && newValue.code.length == 6 && newValue.passwordOne.length >= 8 && newValue.passwordOne == newValue.passwordTwo) {
					this.modifyMobile = true;
				} else {
					this.modifyMobile = false;
				}
			},
			deep: true
		}
	},

	methods: {
		/**
		 * 提交按钮点击执行
		 */
		fnModify() {
			
			
			
			
			var that = this;
			var yan = this.mobile.code;
			var phone = this.mobile.phone;
			var pass = this.mobile.passwordOne;
			var u = {};
			u.phone = phone;
			u.password = pass;
			
			if(this.mobile.passwordOne!=this.mobile.passwordTwo&&this.mobile.passwordOne){
				
			uni.showToast({
				title: '两次密码不一致！',
				image: '../../static/img/alert-3.png',
				duration: 3000
			});
			return ;
				
			}
			if(this.mobile.phone==''||this.mobile.code==''){
				
				uni.showToast({
					title: '验证码和手机号不能为空！',
					image: '../../static/img/alert-3.png',
					duration: 3000
				});
				return;
				
			}




			uni.showLoading({
				title: '更新中'
			});
			uni.request({
				url: this.serverUrl + '/user/update/resetpassword/' + this.mobile.phone + '/' + this.mobile.code, //仅为示例，并非真实接口地址。
				method: 'POST',
				data: u,
				header: {
					'content-type': 'application/json'
					//自定义请求头信息
				},
				success: res => {
					uni.hideLoading();
					if (res.data.status) {
						uni.showToast({
							title: '更新成功：',
							image: '../../static/img/alert-2.png',
							duration: 3000
						});
						uni.switchTab({
							url: '../user/user'
						});
					} else {
						console.log(res.data.message);
						uni.showToast({
							title: '更新失败！',
							image: '../../static/img/alert-3.png',
							duration: 3000
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '更新失败！',
						image: '../../static/img/alert-3.png',
						duration: 3000
					});
				}
			});
		},
		/**
		 * 获取验证码
		 */
		fnGetPhoneCode() {
			if (this.phoneReg.test(this.mobile.phone)) {
			  uni.showToast({
			    title: "正在发送验证码",
			    icon: "loading",
			    success: () => {
			      // 成功后显示倒计时60s后可在点击
			      this.safety.state = true;
			      // 倒计时
			      this.safety.interval = setInterval(() => {
			        if (this.safety.time-- <= 0) {
			          this.safety.time = 60;
			          this.safety.state = false;
			          clearInterval(this.safety.interval);
			        }
			      }, 1000);
				  uni.request({
				  	url: this.serverUrl + '/message/getYanzhengma/' + this.mobile.phone + '/update', //仅为示例，并非真实接口地址。
				  	data: {},
				  	header: {
				  		//自定义请求头信息
				  	},
				  	success: res => {
				  		console.log(res.data);
				  		uni.showToast({
				  			icon: 'none',
				  			title: '发送成功'
				  		});
				  	},
				  	fail(err) {
				  		uni.showToast({
				  			title: '发送失败',
				  			icon: 'none'
				  		});
				  	}
				  });		  
			    }
			  })
			} else {
			  uni.showToast({
			    title: "手机号不正确",
			    icon: "none"
			  })
			}
			
		},
		beforeDestroy() {
			/**
			 * 关闭页面清除轮询器
			 */
			clearInterval(this.safety.interval);
		}
	}
};
